@import 'sanitize.css/sanitize.css';
@import 'sanitize.css/forms.css';
@import 'sanitize.css/assets.css';
@import 'sanitize.css/typography.css';
@import 'sanitize.css/reduce-motion.css';
:root {
  --color-black: #000000;
  --color-white: #ffffff;
  --white: #ffffff;
  --black: #000000;
  --yellow: #f8b462;
  --burgundy: #981140;
  --grey: #f3f3f3;
  --pink: #d91869;
  --background-slider-burgundy: linear-gradient(0deg, #B3315E 0%, #B3315E 100%), #4371AC;
  --font-base: RobotoFlex, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Ubuntu", "Droid Sans", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  --font-notosans: NotoSans, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Ubuntu", "Droid Sans", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  --font-roboto: Roboto, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Ubuntu", "Droid Sans", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  --font-sourceserif: SourceSerifPro, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Ubuntu", "Droid Sans", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  --font-size-root: 100%;
  --font-size-base: 1rem;
  --font-weight-base: 400;
  --line-height-base: 1.25;
}

:root {
  font-size: var(--font-size-root);
}

html,
body {
  height: 100%;
}

html {
  min-width: 320px;
  min-height: 100%;
  font-size: var(--font-size-root);
  font-family: sans-serif;
  line-height: normal;
  -webkit-tap-highlight-color: rgb(var(--color-black) 0);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  color: var(--body-color);
  font-weight: var(--font-weight-base);
  font-size: var(--font-size-base);
  font-family: var(--font-base);
  line-height: var(--line-height-base);
  background-color: var(--body-bg);
}

svg {
  display: block;
}

[tabindex="-1"]:focus:not(:focus-visible) {
  outline: 0;
}

[hidden] {
  display: none;
}

@font-face {
  font-weight: 500;
  font-family: NotoSans;
  font-style: normal;
  font-display: swap;
  src: url("/{{fonts}}/NotoSans-Medium.woff2") format("woff2");
}
@font-face {
  font-weight: 400;
  font-family: NotoSans;
  font-style: normal;
  font-display: swap;
  src: url("/{{fonts}}/NotoSans-Regular.woff2") format("woff2");
}
@font-face {
  font-weight: 400;
  font-family: Roboto;
  font-style: normal;
  font-display: swap;
  src: url("/{{fonts}}/Roboto-Regular.woff2") format("woff2");
}
@font-face {
  font-weight: 400;
  font-family: RobotoFlex;
  font-style: normal;
  font-display: swap;
  src: url("/{{fonts}}/RobotoFlex-Regular.woff2") format("woff2");
}
@font-face {
  font-weight: 700;
  font-family: SourceSerifPro;
  font-style: normal;
  font-display: swap;
  src: url("/{{fonts}}/SourceSerifPro-Bold.woff2") format("woff2");
}
.burger__button {
  position: relative;
  z-index: 40;
  width: 32px;
  height: 32px;
  cursor: pointer;
}
.burger__button span {
  position: absolute;
  top: 50%;
  left: calc(50% - 11px);
  width: 22px;
  height: 2px;
  background-color: var(--color-black, #000);
  transform: translate(0, -50%);
  transition: all 0.5s !important;
}
.burger__button span:nth-of-type(2) {
  top: calc(50% - 7px);
}
.burger__button span:nth-of-type(3) {
  top: calc(50% + 7px);
}
[data-state=mobile-menu] .burger__button--active span:nth-of-type(1) {
  display: none;
}
[data-state=mobile-menu] .burger__button--active span:nth-of-type(2) {
  top: 50%;
  transform: rotate(45deg);
}
[data-state=mobile-menu] .burger__button--active span:nth-of-type(3) {
  top: 50%;
  transform: rotate(-45deg);
}
.burger__menu {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 30;
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  padding: 105px 20px 50px;
  background: var(--white, #fff);
  transform: translate(100%, 0);
  transition: all 0.5s ease-out !important;
}
[data-state=mobile-menu] .burger__menu--active {
  transform: translate(0%, 0);
}
.burger__nav {
  display: grid;
  width: max-content;
}
.burger__nav-link {
  width: fit-content;
  margin-bottom: 20px;
}
.burger__nav-link:last-child {
  margin-bottom: 0;
}
.burger__buttons {
  display: flex;
  gap: 0 8px;
  align-items: center;
  width: fit-content;
  margin-top: 44px;
}
.burger__buttons p {
  margin: 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
}
.burger__social-network-icon {
  width: 22px;
  height: 22px;
}
.burger__social-media {
  display: flex;
  flex-grow: 1;
  gap: 0 8px;
  align-items: flex-end;
  width: 100%;
  margin-top: 40px;
}
.burger__social-media-icon {
  position: relative;
  height: fit-content;
}
.burger__social-media-icon::after {
  position: absolute;
  top: 0;
  right: -4px;
  width: 1px;
  height: 100%;
  background: var(--grey, #f3f3f3);
  content: "";
}
.burger__social-media-icon:last-child::after {
  display: none;
}
.burger__social-media-icon svg {
  width: 48px;
  height: 48px;
}

.buttons {
  padding: 12px 24px;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  letter-spacing: 0.4px;
  text-decoration: none;
  background: none;
  background: var(--grey, #f3f3f3);
  border: none;
  border-radius: 112px;
  cursor: pointer;
  transition: 0.3s ease;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
}
@media screen and (min-width: 0) {
  .container {
    width: calc(100% - 40px);
  }
}

.header {
  position: relative;
  padding: 20px 0 25px;
  background: var(--white, #fff);
}
.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__logo {
  width: 215px;
  height: 44px;
}
@media screen and (min-width: 992px) {
  .header__logo {
    width: 340px;
    height: 75px;
  }
}
.header__container-logo {
  position: relative;
}
.header__container-logo::after {
  position: absolute;
  top: 0;
  right: -71px;
  display: none;
  width: 1px;
  height: 100%;
  background: var(--grey, #f3f3f3);
  content: "";
}
@media screen and (min-width: 992px) {
  .header__container-logo::after {
    display: block;
  }
}
.header__social-network {
  display: none;
  gap: 0 8px;
  align-items: center;
}
@media screen and (min-width: 992px) {
  .header__social-network {
    display: flex;
  }
}
.header__container-icon {
  position: relative;
}
.header__container-icon::after {
  position: absolute;
  top: 0;
  right: -4px;
  width: 1px;
  height: 100%;
  background: var(--grey, #f3f3f3);
  content: "";
}
.header__container-icon:last-child::after {
  display: none;
}
.header__social-network-icon {
  width: 48px;
  height: 48px;
}
.header__burger {
  display: flex;
  gap: 0 8px;
  align-items: center;
}
@media screen and (min-width: 992px) {
  .header__burger {
    display: none;
  }
}
.header__icon-heart {
  width: 32px;
  height: 32px;
  cursor: pointer;
}
.header__icon-heart:hover {
  opacity: 0.7;
}

.heart-animation {
  position: relative;
}
.heart-animation--white .heart-animation__icon {
  fill: var(--white, #ffffff);
}
.heart-animation--white:before, .heart-animation--white:after {
  background-image: url("../img/icon__heart--white.svg");
}
.heart-animation--pink .heart-animation__icon {
  fill: var(--pink, #d91869);
}
.heart-animation--pink:before, .heart-animation--pink:after {
  background-image: url("../img/icon__heart--pink.svg");
}
.heart-animation::before, .heart-animation::after {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 32px;
  height: 32px;
  background-size: 32px 32px;
  z-index: 0;
  transform: translate(-50%, -50%);
  content: "";
}
.heart-animation::before {
  opacity: 0.15;
  animation: heart-animation--before 2.5s ease 100ms infinite !important;
}
.heart-animation::after {
  opacity: 0.25;
  animation: heart-animation--after 2.5s ease 200ms infinite !important;
}
.heart-animation__icon {
  position: relative;
  z-index: 20;
  width: 32px;
  height: 32px;
  animation: heart-animation 2.5s ease 0s infinite !important;
}
@keyframes heart-animation {
  0% {
    transform: scale(1);
  }
  55% {
    transform: scale(1.05);
  }
}
@keyframes heart-animation--before {
  20% {
    transform: translate(-50%, -50%) scale(1);
  }
  75% {
    transform: translate(-50%, -50%) scale(1.8);
  }
}
@keyframes heart-animation--after {
  10% {
    transform: translate(-50%, -50%) scale(1);
  }
  65% {
    transform: translate(-50%, -50%) scale(1.4);
  }
}

.hero {
  background: var(--background-slider-burgundy);
}
.hero__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 40px;
}
.hero__nav {
  display: flex;
  gap: 0 40px;
}
.hero__project-assistance {
  display: flex;
  gap: 0 20px;
  align-items: center;
  cursor: pointer;
}
.hero__project-assistance-text, .hero__nav-link {
  color: var(--white, #ffffff);
  font-size: 18px;
  line-height: 26px; /* 144.444% */
}

.link {
  text-decoration: none;
}
.link:hover {
  opacity: 0.7;
}

